const validate = {
    installed: false,
    install: function (Vue) {
        Object.defineProperty(Vue.prototype, '$validate', {
            get() {
                return {
                    isEmpty(str) {
                        if (str.trim() && str.toString().length > 0) {
                            return false;
                        }
                        return true;
                    },
                    isAmount(str) {
                        return /^\d+(\.\d{1,2})?$/.test(str);
                    },
                    isPhone(str) {
                        return /^(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/.test(str);
                    },
                    isPwd(str) {
                        str = str.toString();
                        return /^\d+\d{5}$/.test(str);
                    },
                    isIDcard(str) {
                        str = str.toString();
                        return /\d{17}[\d|x]|\d{15}/.test(str);
                    }
                };
            }
        });
    }
};

if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(validate);
}
export default validate;



