
<template>
    <div class=" dialog-sms-verify" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="main">
            <div class="hd">
                <a href="javascript:;" class="btn-close" @click=" close() "></a>
                <h2>{{ title }} </h2>
            </div>
            <div class="bd">
                <div v-if="smsId" class="warn">已向您<b>{{ phone }}</b>的手机号码发送序号为<b>{{ smsId }}</b>的验证码，该验证码5分钟内有效</div>
                <div class="sms-cell">
                    <input type="number" v-number="6" placeholder="请输入验证码" max-length="6" v-model=" smsCode " >
                    <a href="javascript:;" class="btn-get" :class="{ disabled: btnGetDisabled } " @click="_getCode()">{{ btnGetText }}</a>
                </div>
                <div v-if="error" class="error">{{ error }}</div>
            </div>
            <div class="ft">
                <mt-button size="large" type="primary" @click="confirm()">{{ btnConfirmText }}</mt-button>
            </div>
        </div>
    </div>
</template>

<script>

// 已向您139****1234的手机号码发送序号为XXX的验证码，该验证码5分钟内有效
export default {
    props: [
        'options'
    ],
    data() {
        return {
            currentShow: false,
            resolve: '',
            reject: '',
            promise: '',
            smsId: '',
            smsCode: '',
            warn: '',
            error: '',
            timer: '',
            defaultCountdown: 90,
            countdown: 90,
            btnGetText: '',
            btnGetDisabled: false
        };
    },
    created() {
        this.btnGetText = this.btnDefaultGetText;
    },
    computed: {
        title () {
            return this.options.title || '输入短信验证码';
        },
        btnConfirmText () {
            return this.options.btnConfirmText || '确定';
        },
        btnDefaultGetText () {
            return this.options.btnGetText || '获取验证码';
        }
    },
    components: {
    },
    methods: {
        _getCode () {
            if( this.btnGetDisabled ){
                return;
            }
            this.btnGetDisabled = true;
            console.log('获取验证码');
            this.error = '';
            this.smsId = '123';
            this.phone = '13812341234';
            this.timer = setInterval(() => {
                this.countdown --;
                if( this.countdown <= 0 ) {
                    this._resetCode();
                    return;
                }
                console.log(this.countdown);
                this.btnGetText = `${this.countdown}S 后重新获取`;
            }, 1000);
        },
        _resetCode () {
            clearInterval( this.timer );
            this.countdown = this.defaultCountdown;
            this.btnGetText = this.btnDefaultGetText;
            this.btnGetDisabled = false;
            this.error = '';
        },
        confirm () {
            if( this.$validate.isEmpty( this.smsCode ) ) {
                this.error = '验证码不能为空';
                return;
            }

            if( this.smsCode != '123456' ) {
                this.error = '验证码输入错误';
                return;
            }
            this.currentShow = false;
            this.resolve('success');
            this._resetCode();
        },
        close () {
            this.currentShow = false;
            this._resetCode();
            this.reject();
        },
        show () {
            this.currentShow = true;
            this.smsId = '';
            this.smsCode = '';
            this._resetCode();
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>



<style lang="scss" scoped>

    $item-height: 80px;

    .dialog-sms-verify
    {
        position: fixed;
        top: 100%;
        left: 0px;
        height: 100%;
        width: 100%;
        z-index: 999;
        opacity: 0;
        

        @include transition(opacity 0.3s);

        &.show
        {
            top: 0px;
            opacity: 1;
        }

        .mask
        {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .main
    {
        @include offset-center();
        @include border-radius( $dialog-border-radius );

        width: 80%;
        z-index: 9;
        background-color: #fff;

        .hd
        {
            position: relative;
            height: $item-height;
            line-height: $item-height;

            @include mini-border(bottom);
            h2
            {
                margin: 0px;
                font-size: 14px;
                font-weight: normal;
                text-align: center;
            }

            .btn-close
            {
                position: absolute;
                top: 0px;
                right: 0px;
                width: $item-height;
                height: 100%;

                background-image: url('../../assets/img/dialog/icon-close.png');

                @include bg-offset-center();

                background-size: 32px 32px;
            }
        }

        .bd
        {
            padding: 30px 15px 10px 15px;

            .error,
            .warn
            {
                margin: 20px 5px;
                color: #f00;
            }
            .warn
            {
                color: #666;
                b
                {
                    padding: 0px 4px;
                    font-weight: normal;
                    color: #d2ae28;
                }
            }
        }

        .ft
        {
            padding: 30px 20px;

            .mint-button
            {
                font-size: 14px;
            }
        }
    }

    .sms-cell
    {
        position: relative;
        padding: 4px 245px 4px 4px;
        height: 100px;
        

        @include mini-border(all);
        @include mini-border(right, #d9d9d9, after);
        &:after
        {
            right: 240px;
        }

        input
        {
            display: block;
            position: relative;
            z-index: 9;
            padding-left: 15px;
            width: 100%;
            height: 90px;
            line-height: normal;
            border: solid 1px none;
            font-size: 14px;
        }

        .btn-get
        {
            position: absolute;
            top: 4px;
            right: 0px;
            z-index: 9;
            width: 230px;
            height: 92px;
            line-height: 92px;
            text-align: center;
            font-size: 13px;

            &.disabled
            {
                opacity: 0.4;
            }
        }
    }
    
</style>



