import Mock from 'mockjs';

Mock.mock('/mock/bank-city/list',
    {"hotlist|10-20": [
        {
            name: '@city',
            cnName: /[A-Z]{10}/,
            'isHot|1': true
        }
    ],
    "list|600-700": [
        {
            name: '@city',
            cnName: /[A-Z]{10}/,
            isHot: false
        }
    ]}
);



