import * as types from '../mutation-types';

const state = {
    bankCity: ''
};

// getters
const getters = {
};

// actions
const actions = {
    updateBankCity: ({ commit }, data ) => {
        commit('UPDATE_BANK_CITY', data);
    },
    clearBankCity: ({ commit }, data ) => {
        commit('CLEAR_BANK_CITY', data);
    }
};

// mutations
const mutations = {
    UPDATE_BANK_CITY (state, data) {
        state.bankCity = data.name;
    },
    CLEAR_BANK_CITY (state, data) {
        state.bankCity = '';
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



