const _standard = 75;
const _flexible = window['lib'].flexible;
const flexible = {
    installed: false,
    install: function( Vue ){
        Object.defineProperty(Vue.prototype, '$flexible', {
            get () {
                return {
                    convert( px ) {
                        return ( _flexible.rem / _standard ) * px;
                    }
                };
            }
        });
    }
};
if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(flexible);
}
export default flexible;



