import axios from 'axios';
import { Indicator } from 'mint-ui-rem-dpr';

const defaultLoading = '正在提交...';

axios.defaults.baseURL = '/mock';

// Add a request interceptor
axios.interceptors.request.use(function (config) {
    // Do something before request is sent
    config.transformRequest = function(data, headers) {
        let laodingText = '';
        if ( data && data.showLoading === true) {
            laodingText = defaultLoading;
        } else if ( data && typeof data.showLoading === 'string') {
            laodingText = data.showLoading;
        }
        if ( data && data.showLoading ) {
            Indicator.open({
                text: laodingText,
                spinnerType: 'fading-circle'
            });
        }
    };
    return config;
}, function (error) {
    // Do something with request error
    return Promise.reject(error);
});

// Add a response interceptor
axios.interceptors.response.use(function (response) {
    Indicator.close();
    // Do something with response data
    return response;
}, function (error) {
    // Do something with response error
    return Promise.reject(error);
});

const ajax = {
    post ( url, data ) {
        return new Promise(function(resolve, reject) {
            axios.post( url, data).then((res) => {
                resolve(res);
            }, (res) => {
                reject(res);
            }).catch((err) => {
                reject(err);
            });
        });
    }
};
export default ajax;


