<template>
    <div class="selector-bank-city">
        <div class="fixed-content">
            <div class="search-bar">
                <div class="input-border">
                    <input type="search" placeholder="搜索..." v-model="search">
                </div>
            </div>
        </div>

        <div class="all-bank-city">
            <better-scroll ref="bankCityList" :data="bankCityList" :probeType=" probeType " :listenScroll=" listenScroll "  @scroll="bankCityListScroll" > 
                <div class="bank-list">
                    <template v-for="( list, section ) in bankCityListSection">
                        <h4 class="division" key=" section " :data-anchor=" section " >{{section === 'hot' ? '热门银行' : section }}</h4>
                        <ul v-if=" section == 'hot' " class="hot-list" >
                            <li class="item" v-for=" ( item, index ) in  bankCityListSection[section] " >
                                <a href="javascript:;" @click="bankClick( item )">
                                    <cite>{{ item.name }}</cite>
                                </a>
                            </li>
                        </ul>
                        <ul v-if=" section != 'hot' " class="list" >
                            <li class="item" v-for=" ( item, index ) in  bankCityListSection[section] " >
                                <a href="javascript:;" @click="bankClick( item )">
                                    <cite>{{ item.name }}</cite>
                                </a>
                            </li>
                        </ul>
                    </template>
                </div>
            </better-scroll>
            <div class="shortcut" ref="shortcut">
                <ul class="list">
                    <li class="item" :class="{ active : item == anchorActive }" v-for=" item in anchorList" :data-anchor="item">{{ item === 'hot' ? '热' : item }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as serviceBankCtiy from '@/services/bank-city';

export default {
    data() {
        return {
            routerFrom: '',
            probeType: 3,
            listenScroll: true,
            bankCityList: [],
            bankCityListSection: [],
            anchorList: [],
            anchorMap: {},
            anchorActive: '',
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 48,
            itemHeight: 80,
            hotItemHeight: 75,
            anchorHeight: 32
        };
    },
    created(){
        serviceBankCtiy.getBankCityList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.hotlist.concat( res.data.list );
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });

            let _listSection = { hot: [] };
            let _anchorList = [ 'hot' ];
            _temp.forEach((item, index) => {
                if( item.isHot ) {
                    _listSection.hot.push( item );
                }
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _hotItemHeight = this.$flexible.convert( this.hotItemHeight );
            let _hotFixHeight = this.$flexible.convert( 15 );
            let _fix = _temp.length / 3000;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
                if ( item === 'hot' ) {
                    _y -= _divisionHeight + _hotFixHeight + Math.ceil(_listSection[item].length / 4 ) * _hotItemHeight;
                } else {
                    _y -= _divisionHeight + _listSection[item].length * _itemHeight;
                }
                _y += _fix;
            }
            this.bankCityList = [ 1 ];
            this.bankCityListSection = _listSection;
            this.anchorList = _anchorList;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.all-bank-city .better-scroll-wrapper').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.all-bank-city .bank-list').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
        _anchorHeight () {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * 32;
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.bankCityList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        bankClick ( data ) {
            this.$store.dispatch('updateBankCity', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        bankCityListScroll (data) {
            this._currentAnchor( data.y );
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-bank-city
    {
        position: relative;
        padding-top: 80px;
        height: 100%;
    }
    .fixed-content
    {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 330px;
    }
    .search-bar
    {
        position: relative;
        padding: 13px 20px;
        height: 80px;
        background-color: #fff;

        @include mini-border(bottom);

        .input-border
        {
            position: relative;
            padding: 2px 5px;
            height: 54px;
            @include mini-border(all, #d9d9d9, before, 5px);
            
            input
            {
                position: relative;
                z-index: 9;
                display: block;
                padding: 10px;
                width: 100%;
                height: 50px;
                line-height: normal;
                border: none;
            }
        }
    }
    .all-bank-city
    {
        position: relative;
        padding-bottom: 10px;
        height: 100%;
        background-color: #fff;

        .better-scroll-wrapper
        {
            position: relative;
            height: 100%;
            overflow: hidden;
        }

        .bank-list
        {
            .division
            {
                padding-left: 20px;
                margin: 0px;
                height: 48px;
                line-height: 48px;
                background-color: #f2f2f2;
                font-size: 13px;
            }
            .list
            {
                .item
                {
                    position: relative;
                    @include mini-border(top);
                    a
                    {
                        position: relative;
                        display: block;
                        padding: 10px 20px;
                        height: 80px;
                        line-height: 60px;
                        overflow: hidden;

                        cite
                        {
                            font-size: 13px;
                        }
                    }
                }

                .item:first-child
                {
                    a
                    {
                        &:before
                        {
                            content: none;
                        }
                    }
                }
            }

            .hot-list
            {
                margin-top: 15px;
                @include clearfix();
                .item
                {
                    float: left;
                    margin-bottom: 15px;
                    width: 25%;
                    height: 60px;
                    a
                    {
                        position: relative;
                        display: block;
                        margin: 0px 10px;
                        padding: 0px 5px;
                        height: 60px;
                        line-height: 60px;
                        text-align: center;

                        @include mini-border(all);

                        cite
                        {
                            display: block;

                            @include ellipsis();
                        }
                    }
                }

                .item:nth-of-type(4n+4)
                {
                    &:after
                    {
                        content: none;
                    }   
                }
            }

        }
    }

    .shortcut
    {
        @include offset-center();

        left: auto;
        right: 0px;
        @include transform(translateY(-45%));

        .list
        {
            .item
            {
                height: 32px;
                line-height: 32px;
                width: 60px;
                text-align: center;
                color: #666;
                &.active
                {
                    color: #4893E3;
                }
            }
        }
    }
    
</style>



