const state = {
    search: {
        data: []
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateMessageCenterSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearMessageCenterSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search.data = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            data: []
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



