const state = {
    search: {
        accountList: [],
        accountInfo: {
            account: '',
            accountType: '',
            operationType: '',
            userName: '',
            userIdNo: '',
            phone: '',
            action: '',
            type: ''
        }
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateRelationshipChangeSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearRelationshipChangeSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            accountList: [],
            accountInfo: {
                account: '',
                accountType: '',
                operationType: '',
                userName: '',
                userIdNo: '',
                phone: '',
                action: '',
                type: ''
            }
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



