import Vue from 'vue';

Date.prototype.format = function (fmt) {
    var o = {
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "H+": this.getHours(),
        "m+": this.getMinutes(),
        "s+": this.getSeconds(),
        "q+": Math.floor((this.getMonth() + 3) / 3),
        "S": this.getMilliseconds()
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o) if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
};

Date.prototype.Day = (val, x) => {
    val.setDate(val.getDate() - x);
    return val;
};

Date.prototype.Week = (val, x) => {
    val.setDate(val.getDate() - 7 * x);
    return val;
};

Date.prototype.Month = (val, x) => {
    val.setMonth(val.getMonth() - 1 * x);
    return val;
};

Date.prototype.Year = (val, x) => {
    val.setFullYear(val.getFullYear() - 1 * x);
    return val;
};

Vue.filter('formatDate', (date, format) => {
    if (!date) {
        return '';
    }
    var _date = null;
    if (/^\d+$/.test(date)) {
        _date = new Date(date);
    } else {
        _date = new Date(date.replace(/-/g, '/'));
    }

    return _date.format(format);
});


Vue.filter('thousands', function (str, fixed) {
    let thousandsRE = /\d{1,3}(?=(\d{3})+$)/g;
    let pointRe = /^(\d+)((\.\d+)?)$/;

    let replaceStr = '$&,';
    let numberVal = str * 1;
    let string = numberVal >= 0 ? String(str) : String(str).slice(1);
    let value = string.replace(pointRe, function (str, s1, s2) {
        return s1.replace(thousandsRE, replaceStr) + s2;
    });
    return numberVal >= 0 ? value : '-' + value;
});

Vue.filter('accountTypeFilter', function (input) {
    var map = {
        '1': 'Ⅰ类',
        '2': 'Ⅱ类',
        '3': 'Ⅲ类'
    };
    if(map[input] == null){
        return '--';
    }else{
        return map[input];
    }
});

Vue.filter('formatAccount', function (input) {
    if (!input) {
        return '';
    }
    return input.replace(/(\d{4})(?=\d)/g, "$1 ");
});

Vue.filter('number', function (val, number) {
    return val ? Number(val).toFixed(number) : val;
});

Vue.filter('keepType', function(val){
    let map = {
        '1': '整存整取',
        '2': '定活两便',
        '3': '零存整取'
    };
    return val ? map[val] : val;
});


