
<template>
    <div class=" dialog-agreement" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="main">
            <div class="hd">
                <a href="javascript:;" class="btn-close" @click=" close() "></a>
                <h2>{{ title }} </h2>
            </div>
            <div class="bd">
                <better-scroll :data="tempData" :bounce="bounce" >
                    <div class="content" v-html="contentHtml"></div>
                </better-scroll>
            </div>
            <div class="ft">
                <mt-button size="large" type="primary" @click="confirm()">{{ btnConfirmText }}</mt-button>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    props: [
        'options'
    ],
    data() {
        return {
            bounce: false,
            tempData: [],
            contentHtml: '',
            currentShow: false,
            resolve: '',
            reject: '',
            promise: ''
        };
    },
    created() {
        this.btnGetText = this.btnDefaultGetText;
    },
    computed: {
        title () {
            return this.options.title || '协议';
        },
        btnConfirmText () {
            return this.options.btnConfirmText || '同意';
        }
    },
    components: {
    },
    methods: {
        confirm () {
            this.currentShow = false;
            this.resolve('success');
        },
        close () {
            this.currentShow = false;
            this.reject();
        },
        show ( contentHtml ) {
            this.tempData.push(1);
            this.contentHtml = contentHtml;
            this.currentShow = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>

<style lang="scss" scoped>

    $item-height: 80px;

    .dialog-agreement
    {
        position: fixed;
        top: 100%;
        left: 0px;
        height: 100%;
        width: 100%;
        z-index: 999;
        opacity: 0;
        

        @include transition(opacity 0.3s);

        &.show
        {
            top: 0px;
            opacity: 1;
        }

        .mask
        {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .main
    {
        @include offset-center();
        @include border-radius( $dialog-border-radius );

        width: 80%;
        z-index: 9;
        background-color: #fff;

        .hd
        {
            position: relative;
            height: $item-height;
            line-height: $item-height;

            @include mini-border(bottom);
            h2
            {
                margin: 0px;
                font-size: 14px;
                font-weight: normal;
                text-align: center;
            }

            .btn-close
            {
                position: absolute;
                top: 0px;
                left: 0px;
                width: $item-height;
                height: 100%;

                background-image: url('../../assets/img/dialog/icon-close.png');

                @include bg-offset-center();

                background-size: 32px 32px;
            }
        }

        .bd
        {
            padding: 20px 15px 20px 15px;

            .error,
            .warn
            {
                margin: 20px 5px;
                color: #f00;
            }
            .warn
            {
                color: #666;
                b
                {
                    padding: 0px 4px;
                    font-weight: normal;
                    color: #d2ae28;
                }
            }

            .better-scroll-wrapper
            {
                min-height: 300px;
                max-height: 700px;
                overflow: hidden;
            }
        }

        .ft
        {
            padding: 0px;

            .mint-button
            {
                font-size: 14px;

                @include border-radius(0px 0px $dialog-border-radius $dialog-border-radius);
                
            }
        }
    }

    
</style>



