<template>
    <div class="cell-date-zone">
        <label>{{ title }}</label>
        <ul class="zone-list">
            <li class="item" v-for="( item, index ) in zoneList" :key="index">
                <mt-button v-if=" item.id ===  checkedId " size="small" type="primary" @click="change( item.id )">{{ item.text }}</mt-button>
                <mt-button v-if=" item.id !==  checkedId " size="small" type="default" @click="change( item.id )">{{ item.text }}</mt-button>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: [
        'title',
        'zone'
    ],
    data() {
        return {
            resolve: '',
            reject: '',
            promise: '',
            checkedId: -1,
            zoneList: [],
            zoneText: {
                '1W': '近一周',
                '1M': '近一月',
                '3M': '近三月'
            }
        };
    },
    created() {
        let _zone = (this.zone || '1W,1M,3M').replace(/\s/, '');
        _zone.split(',').forEach((item) => {
            this.zoneList.push({
                id: item,
                text: this.zoneText[ item ]
            });
        });
        console.log(this.zoneList);
    },
    methods: {
        setDefault (begin, end) {
            var now = new Date().format('yyyy-MM-dd');
            if( now !== end ){
                this.change( this.zoneList[0].id );
                return;
            }
            if( begin === this._getDateZoneWeek() ){
                this.checkedId = '1W';
            }
            if( begin === this._getDateZoneMonth() ){
                this.checkedId = '1M';
            }
            if( begin === this._getDateZone3Month() ){
                this.checkedId = '3M';
            }
        },
        change ( id ) {
            this.checkedId = id;
            var begin = 0;
            var end = new Date().format('yyyy-MM-dd');

            if ( id === '1W' ) {
                begin = this._getDateZoneWeek();
            } else if ( id === '1M' ){
                begin = this._getDateZoneMonth();
            } else if ( id === '3M' ){
                begin = this._getDateZone3Month();
            }
            console.log('begin：' + new Date(begin).format('yyyy-MM-dd') + ' -  end：' + new Date(end).format('yyyy-MM-dd') );
            this.$emit('date-zone-change', begin, end );
        },
        _getDateZoneWeek (){
            var now = new Date();
            var day = now.getDate();
            return new Date( now.setDate( day - 7 ) ).format('yyyy-MM-dd');
        },
        _getDateZoneMonth (){
            var now = new Date();
            var day = now.getDate();
            return new Date( now.setDate( day - 30 ) ).format('yyyy-MM-dd');
        },
        _getDateZone3Month (){
            var now = new Date();
            var month = now.getMonth();
            return new Date( now.setMonth( month - 3 ) ).format('yyyy-MM-dd');
        }
    }
};

</script>

<style lang="scss" scoped>

    .cell-date-zone
    {
        position: relative;
        padding: 20px 15px;
        background-color: #fff;

        @include mini-border(bottom, #d9d9d9, after);
        @include mini-border(top, #d9d9d9, before);

        label
        {
            font-size: 14px;
        }
    }

    .zone-list
    {
        text-align: right;
        .item
        {
            display: inline-block;
            margin-left: 40px;
            text-align: center;

            .mint-button
            {
                padding: 0px 24px;
                height: 48px;
                font-size: 12px;
            }
        }
        .item:first-child
        {
            text-align: left;
        }
    }
</style>


