import ajax from './ajax';
import api from './api';

export function getTestIndex(data) {
    return new Promise(function(resolve, reject) {
        ajax.post(api.getTestIndex, data).then((res) => {
            resolve(res);
        }, (res) => {
            reject(res);
        }).catch((err) => {
            reject(err);
        });
    });
}





