const state = {
	account: ''
};

// getters
const getters = {
};

// actions
const actions = {
    updateSearchCustom: ({ commit }, data) => {
        commit('UPDATE', data);
    }
};

// mutations
const mutations = {
    UPDATE (state, data) {
        state[data.key] = data.value;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



