<template>
    <div class=" select-payment-account-inline" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="content">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click=" close() ">取消</a>
                <h2>{{ options.title || this.textMap[ this.type ].title }}</h2>
            </div>
            <div class="bd">
                <ul class="list">
                    <li class="item" :class="{ checked: item.id == checkedId  }"  v-for="( item, index ) in accountList " :key=" item.id ">
                        <a href="javascript:;" @click="change( item, index )" >
                            <cite>{{ item.bankName }}({{ item.account }})</cite>
                            <span>{{ item.alias || '未设置别名' }}</span>
                        </a>
                    </li>
                    <li class="item add" v-if=" showAddBtn ">
                        <a href="javascript:;">{{ this.textMap[ this.type ].addBtn }}</a>
                    </li>
                    <li class="item warn" v-if=" showWarn && this.textMap[ this.type ].warn ">{{ this.textMap[ this.type ].wran }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>


export default {
    props: [
        'options'
    ],
    data() {
        return {
            currentShow: false,
            resolve: '',
            reject: '',
            promise: '',
            textMap: {
                'payment': {
                    title: '选择付款账号',
                    addBtn: '+ 添加本行储蓄卡付款',
                    wran: '+ 付款账户仅限已加挂的储蓄卡'
                },
                'receipt': {
                    title: '选择收款账号',
                    addBtn: '+ 添加本行储蓄卡收款',
                    wran: '+ 收款账户仅限已加挂的储蓄卡'
                },
                'credit': {
                    title: '选择信用卡',
                    addBtn: '+ 添加信用卡'
                }
            }
        };
    },
    computed: {
        type () {
            return this.options.type || 'payment';
        },
        showAddBtn () {
            return this.options.showAddBtn === undefined ? true : this.options.showAddBtn;
        },
        showWarn () {
            return this.options.showWarn === undefined ? true : this.options.showWarn;
        },
        checkedId () {
            if( this.options.checked ) {
                return this.options.checked.id || -1;
            }
            return -1;
        },
        accountList (){
            if ( !this.options.accountList || !(this.options.accountList instanceof Array) ) {
                return [];
            } else {
                return this.options.accountList;
            }
        }
    },
    components: {
    },
    methods: {
        change (item, index) {
            this.currentShow = false;
            this.resolve({ item, index });
        },
        close () {
            this.currentShow = false;
            this.reject();
        },
        show () {
            this.currentShow = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>



<style lang="scss" scoped>

    $item-height: 80px;

    .select-payment-account-inline
    {
        position: fixed;
        top: 100%;
        left: 0px;
        right: 0px;
        bottom: 0px;
        z-index: 999;
        opacity: 0;
        overflow: hidden;
        

        @include transition(opacity 0.3s);

        &.show
        {
            top: 0px;
            opacity: 1;
            .content
            {
                top: auto;
                bottom: 0px;
            }
        }

        .mask
        {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .content
    {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 100%;
        z-index: 9;
        background-color: #fff;

        .hd
        {
            position: relative;
            height: $item-height;
            line-height: $item-height;

            @include mini-border(bottom);
            h2
            {
                margin: 0px;
                font-size: 14px;
                font-weight: normal;
                text-align: center;
            }

            .btn-cancel
            {
                position: absolute;
                top: 0px;
                left: 0px;
                padding: 0px 20px;
                height: 100%;
                color: #4893E3;
            }
        }

        .bd
        {
        }
    }

    .list
    {
        .item
        {
            a
            {
                display: block;
                position: relative;
                padding: 8px 20px 5px 20px;
                min-height: $item-height;

                @include mini-border(bottom);

                cite
                {
                    display: block;
                    color: #333;
                }
                span
                {
                    color: #888;
                    font-size: 10px;
                }
                
            }
        }

        .item.add
        {
            a
            {
                line-height: $item-height;
                color: #4893E3;
            }
        }

        .item.warn
        {
            height: $item-height;
            line-height: $item-height;
            text-align: center;
            font-size: 10px;
            color: #888;
        }

        .item.checked
        {
            a
            {
                &:after
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 20px;

                    width: 40px;
                    height: 40px;
                    
                    @include bg-offset-center();
                    @include transform(translateY(-50%));

                    background-image: url('../../assets/img/selector/icon-item-checked.png');
                    
                }
            }
        }
    }
    
</style>



