<template>
    <div class="selector-payee">
        <div class="fixed-content">
            <div class="search-bar">
                <div class="input-border">
                    <input type="search" placeholder="搜索..." v-model="search">
                </div>
            </div>
            <h3 class="title">最近收款人</h3>
            <div class="lately-payee">
                <better-scroll :data="latelyPayeeList" :scrollX="scrollX">
                    <ul class="payee-list" :style="{ width: latelyPayeeListWidth }"  >
                        <li class="item" v-for=" ( item, index) in latelyPayeeList ">
                            <a href="javascript:;" @click="payeeClick( item )" >
                                <cite>{{ item.name }}</cite>
                                <div class="attr">
                                    <i class="logo"></i>
                                    <span>{{ item.bankName }}</span>
                                    <span>{{ item.account }}</span>
                                </div>
                            </a>
                        </li>
                    </ul>
                </better-scroll>
            </div>
            <h3 class="title">历史收款人</h3>
        </div>

        <div class="history-payee">
            <better-scroll ref="historyPayeeList" :data="latelyPayeeList" :probeType=" probeType " :listenScroll=" listenScroll "  @scroll="historyPayeeListScroll" > 
                <div class="payee-list">
                    <template v-for="( list, section ) in historyPayeeList">
                        <h4 class="division" key=" section " :data-anchor=" section " >{{ section }}</h4>
                        <ul class="list">
                            <li class="item" v-for=" ( item, index ) in  historyPayeeList[section] " >
                                <a href="javascript:;" @click="payeeClick( item )">
                                    <i class="logo"></i>
                                    <div class="attr">
                                        <cite>{{ item.name }}</cite>
                                        <span>{{ item.bankName }}({{ item.account }})</span>
                                    </div>
                                </a>
                            </li>
                        </ul>
                    </template>
                </div>
            </better-scroll>
            <div class="shortcut" ref="shortcut">
                <ul class="list">
                    <li class="item" :class="{ active : item == anchorActive }" v-for=" item in anchorList" :data-anchor="item">{{ item }}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as servicePayee from '@/services/payee';

export default {
    data() {
        return {
            routerFrom: '',
            scrollX: true,
            probeType: 3,
            listenScroll: true,
            payeeList: [],
            latelyPayeeList: [],
            historyPayeeList: [],
            anchorList: [],
            latelyPayeeListWidth: '0px',
            anchorMap: {},
            anchorActive: '',
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 48,
            itemHeight: 108,
            anchorHeight: 32
        };
    },
    created(){
        servicePayee.getPayeeList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.hotlist.concat( res.data.list );
            _temp = _temp.sort((a, b) => {
                let _x = a.cnName.substr(0, 1);
                let _y = b.cnName.substr(0, 1);
                let _t = ( _x === _y ) ? 0 : ( _x < _y ? -1 : 1 );
                return _t;
            });
            let _latelyPayeeList = _temp.filter((item) => { return item.isLately === true });

            this.latelyPayeeListWidth = this.$flexible.convert( _latelyPayeeList.length * 355 ) + 'px';
            this.latelyPayeeList = _latelyPayeeList;

            let _listSection = {};
            let _anchorList = [ '★' ];
            _temp.forEach((item, index) => {
                let _x = item.cnName.substr(0, 1).toUpperCase();
                if( !(_x in _listSection) ){
                    _listSection[ _x ] = [];
                    _anchorList.push( _x );
                }
                _listSection[ _x ].push( item );
            });
            let _y = 0;
            let _divisionHeight = this.$flexible.convert( this.divisionHeight );
            let _itemHeight = this.$flexible.convert( this.itemHeight );
            let _fix = _temp.length / 2200;
            for(var item in _listSection ){
                this.anchorMap[ item ] = _y;
                _y -= _divisionHeight + _listSection[item].length * _itemHeight;
                _y += _fix;
            }
            this.historyPayeeList = _listSection;
            this.anchorList = _anchorList;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
        this.bswHeight = document.querySelector('.history-payee .better-scroll-wrapper').clientHeight;
        let _firstTouch;
        let _touch = {};
        let _anchorHeight = this.$flexible.convert( this.anchorHeight );
        this.$refs.shortcut.addEventListener('touchstart', (e) => {
            this.bscHeight = document.querySelector('.history-payee .payee-list').clientHeight;
            let _anchor = e.target.getAttribute('data-anchor');
		    _firstTouch = e.touches[0];
		    _touch.y1 = _firstTouch.pageY;
		    _touch.anchor = _anchor;
            this._scrollTo( _anchor );
        });
        this.$refs.shortcut.addEventListener('touchmove', (e) => {
            _firstTouch = e.touches[0];
		    _touch.y2 = _firstTouch.pageY;
		    let _delta = (_touch.y2 - _touch.y1) / _anchorHeight | 0;
		    let _anchor = this.anchorList[ this.anchorList.indexOf( _touch.anchor ) + _delta ];
            this._scrollTo( _anchor );
		    e.preventDefault();
		    e.stopPropagation();
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
        _latelyPayeeListWidth ( length ) {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * length * 355 + 'px';
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.historyPayeeList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        payeeClick ( data ) {
            this.$store.dispatch('updatePayee', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        historyPayeeListScroll (data) {
            this._currentAnchor( data.y );
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-payee
    {
        position: relative;
        padding-top: 330px;
        height: 100%;
    }
    .fixed-content
    {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 330px;
    }
    .search-bar
    {
        position: relative;
        padding: 13px 20px;
        height: 80px;
        background-color: #fff;

        @include mini-border(bottom);

        .input-border
        {
            position: relative;
            padding: 2px 5px;
            height: 54px;
            @include mini-border(all, #d9d9d9, before, 5px);
            
            input
            {
                position: relative;
                z-index: 9;
                display: block;
                padding: 10px;
                width: 100%;
                height: 50px;
                line-height: normal;
                border: none;
            }
        }
    }
    .title
    {
        padding-left: 20px;
        width: 100%;
        margin: 0px;    
        line-height: 55px;
        font-size: 13px;
        background-color: #fff;

        @include mini-border(top);
    }

    .lately-payee,
    .history-payee
    {
        position: relative;
        padding: 0px 20px 10px 20px;
        background-color: #fff;
    }
    .lately-payee
    {
        position: relative;
        width: 100%;
        height: 140px;
        overflow: hidden;

        @include mini-border(bottom);
        
        .payee-list
        {
            overflow-x: auto;
            white-space: nowrap;
            height: 130px;
            .item
            {
                display: inline-block;
                a
                {
                    position: relative;
                    display: block;
                    padding: 10px 15px;
                    height: 125px;
                    width: 340px;

                    @include mini-border(all, #d9d9d9, before, 5px);

                    cite
                    {
                        display: block;
                        font-size: 13px;
                        line-height: 36px;
                    }

                    .attr
                    {
                        position: relative;
                        padding-top: 5px;
                        padding-left: 70px;
                        margin-top: 5px;
                        .logo
                        {
                            position: absolute;
                            top: 5px;
                            left: 0px;
                            width: 60px;
                            height: 60px;

                            background-image: url('../../assets/img/icon-bank-logo.png');
                            @include border-radius(70px);

                            @include bg-offset-center();
                        }

                        span
                        {
                            display: block;
                            font-size: 11px;
                            line-height: 1.3em;
                        }
                    }
                }
            }

            .item + .item
            {
                margin-left: 15px;
            }
        }
    }
    .history-payee
    {
        padding-left: 0px;
        padding-right: 0px;
        height: 100%;

        .better-scroll-wrapper
        {
            position: relative;
            height: 100%;
            overflow: hidden;
        }

        .payee-list
        {
            .division
            {
                padding-left: 20px;
                margin: 0px;
                height: 48px;
                line-height: 48px;
                background-color: #f2f2f2;
                font-size: 13px;
            }
            .item
            {
                a
                {
                    position: relative;
                    display: block;
                    padding: 10px 15px;
                    padding-left: 120px;
                    height: 108px;
                    overflow: hidden;

                    @include mini-border(top);
                    &:before
                    {
                        left: 20px;
                    }

                    .logo
                    {
                        position: absolute;
                        top: 10px;
                        left: 10px;
                        width: 80px;
                        height: 80px;

                        background-image: url('../../assets/img/icon-bank-logo.png');
                        @include border-radius(70px);

                        @include bg-offset-center();
                    }
                    .attr
                    {
                        cite,span
                        {
                            display: block;
                        }
                        cite
                        {
                            font-size: 13px;
                            line-height: 50px;
                        }
                        span
                        {
                            color: #666;
                            font-size: 11px;
                        }
                    }
                }
            }

            .item:first-child
            {
                a
                {
                    &:before
                    {
                        content: none;
                    }
                }
            }

        }
    }

    .shortcut
    {
        @include offset-center();

        left: auto;
        right: 0px;
        @include transform(translateY(-55%));

        .list
        {
            .item
            {
                height: 32px;
                line-height: 32px;
                width: 60px;
                text-align: center;
                color: #666;
                &.active
                {
                    color: #4893E3;
                }
            }
        }
    }
    
</style>



