const state = {
    payeeName: '',
    payeeAccount: '',
    payeeBankName: ''
};

// getters
const getters = {
};

// actions
const actions = {
    updatePayee: ({ commit }, data ) => {
        commit('UPDATE_PAYEE', data);
    },
    clearPayee: ({ commit }, data ) => {
        commit('CLEAR_PAYEE', data);
    }
};

// mutations
const mutations = {
    UPDATE_PAYEE (state, data) {
        state.payeeName = data.name;
        state.payeeAccount = data.account;
        state.payeeBankName = data.bankName;
    },
    CLEAR_PAYEE (state, data) {
        state.payeeName = '';
        state.payeeAccount = '';
        state.payeeBankName = '';
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



