<template>
    <div class="shortcut-editor" :class="{ 'show-editor' : dataForm.editor }">
        <div class="fun-block fixed">
            <h2>首页常驻功能</h2>
            <!--<ul class="list">
                <li class="item" v-for=" item in fixedMenuList " :key="item.id" >
                    <div class="fun">
                        <i class="icon"></i>  
                        <cite>{{ item.name }}</cite>
                        <a href="javascript:;" class="btn-delete" @click="deleteMenu( item )" ></a>
                    </div>
                </li>
            </ul>-->
            <draggable element="ul" class="list" :class="{ dragging: dataForm.draggableStart }" 
                :options="draggable"
                v-model="fixedMenuList"  @start="draggableStart()" @end="draggableEnd()" >
                <transition-group type="transition" :name="'draggable'">
                    <li class="item" v-for="(item, index) in fixedMenuList" :key="item.id">
                        <div class="fun">
                            <i class="icon"></i>
                            <cite>{{item.name}}</cite>
                            <a href="javascript:;" class="btn-delete" @click="deleteMenu(item, index )" ></a>
                        </div>
                    </li class="item">
                </transition-group>
            </draggable>

            <div class="wran" v-if="this.dataForm.editor">
                最多支持在首页设置7个常驻功能 <br>按住拖动可调整顺序
            </div>

            <a href="javascript:;" class="bnt-editor" @click="editor()">{{ this.dataForm.editor ? '取消' : '编辑' }}</a>
        </div>
        
        <div class="fun-block" v-for=" (menu, index) in menuList " :key="index" >
            <h2>{{ menu.title }}</h2>
            <ul class="list">
                <li class="item" v-if=" item.isFixed === false  " v-for=" item in menu.list " :key="item.id">
                    <div class="fun">
                        <i class="icon"></i>  
                        <cite>{{ item.name }}</cite>
                        <a href="javascript:;" class="btn-add" @click="addMenu( item )" ></a>
                    </div>
                </li>
            </ul>
        </div>

    </div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';

import draggable from 'vuedraggable';

export default {
    data () {
        return {
            dataForm: {
                editor: false,
                draggableStart: false
            },
            menuList: [
                {
                    title: '转账',
                    list: [
                        {
                            id: 11,
                            name: '转账汇款',
                            isFixed: true,
                            sortIndex: 2,
                            group: 1
                        },
                        {
                            id: 12,
                            name: '本人账户互转',
                            isFixed: false,
                            group: 1
                        },
                        {
                            id: 13,
                            name: '行内互转',
                            isFixed: false,
                            group: 1
                        },
                        {
                            id: 14,
                            name: '行外互转',
                            isFixed: false,
                            group: 1
                        },
                        {
                            id: 15,
                            name: '定向大额转账',
                            isFixed: false,
                            group: 1
                        },
                        {
                            id: 16,
                            name: '农信银转账',
                            isFixed: false,
                            group: 1
                        }
                    ]
                },
                {
                    title: '查询',
                    key: '2',
                    list: [
                        {
                            id: 21,
                            name: '交易查询',
                            isFixed: true,
                            sortIndex: 1,
                            group: 2
                        },
                        {
                            id: 22,
                            name: '手机交易记录',
                            isFixed: false,
                            group: 2
                        }
                    ]
                },
                {
                    title: '贷款',
                    key: '2',
                    list: [
                        {
                            id: 31,
                            name: '金农易贷',
                            isFixed: false,
                            group: 3
                        },
                        {
                            id: 32,
                            name: '我的贷款',
                            isFixed: false,
                            group: 3
                        }
                    ]
                },
                {
                    title: '生活缴费',
                    key: '4',
                    list: [
                        {
                            id: 41,
                            name: '手机充值',
                            isFixed: true,
                            sortIndex: 3,
                            group: 4
                        },
                        {
                            id: 42,
                            name: '电费',
                            isFixed: false,
                            group: 4
                        }
                    ]
                }
            ],
            fixedMenuList: [
            ]
        };
    },
    created (){
        this.fixedMenuList = this.getFixedMenuList();
    },
    computed: {
        draggable () {
            return {
                disabled: !this.dataForm.editor
            };
        }
    },
    methods: {
        getFixedMenuList () {
            let _temp = [];
            this.menuList.forEach(( menu ) => {
                menu.list.forEach( ( item ) => {
                    if( item.isFixed ) {
                        _temp.push( Object.assign( {}, item ) );
                    }
                });
            });

            _temp.sort((a, b) => {
                return a.sortIndex - b.sortIndex;
            });
            return _temp;
        },
        editor () {
            this.dataForm.editor = !this.dataForm.editor;
            this.fixedMenuList.forEach((item, index) => {
                item.sortIndex = index + 1;
            });
        },
        addMenu ( menu ) {
            if ( this.fixedMenuList.length >= 7 ) {
                Toast({
                    message: '常驻菜单不能超过7个',
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
            let _index = this.fixedMenuList.findIndex((item) => {
                return item.id == menu.id;
            });
            if ( _index === -1) {
                menu.isFixed = true;
                menu.sortIndex = this.fixedMenuList.length + 1;
                this.fixedMenuList.push( Object.assign( {}, menu ) );
            }
        },
        deleteMenu ( menu, index ) {
            if ( this.fixedMenuList.length <= 3 ) {
                Toast({
                    message: '常驻菜单不能少于3个',
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
            this.fixedMenuList.splice(index, 1);
            this.menuList.forEach(( m ) => {
                m.list.forEach( ( item ) => {
                    if( item.id === menu.id ) {
                        item.isFixed = false;
                        item.sortIndex = '';
                    }
                });
            });
        },
        draggableStart () {
            this.dataForm.draggableStart = true;
        },
        draggableEnd () {
            this.dataForm.draggableStart = false;
            this.fixedMenuList.forEach((item) => {
                console.log(item.name);
            });
        }
    },
    components: {
        draggable
    }
};
</script>

<style lang="scss">
    .fun-block
    {
        position: relative;
        padding: 12px;
        background-color: #fff;
        @include mini-border(top, #d9d9d9, before );
        @include mini-border(bottom, #d9d9d9, after );

        &.fixed
        {
            background-color: #DFDFDF;

            .list
            {
                .item
                {
                    .fun
                    {
                        background-color: #DFDFDF;
                    }
                }
            }
        }

        .bnt-editor
        {
            position: absolute;
            top: 15px;
            right: 20px;
        }

        h2
        {
            line-height: 40px;
            color: #777;
            font-size: 12px;
        }

        .list
        {
            padding: 10px 30px 20px 30px;

            @include clearfix();

            .item
            {
                float: left;
                margin-top: 10px;
                padding: 5px 20px;
                width: 25%;
                .fun
                {
                    position: relative;
                    padding: 10px 0px;
                    width: 100%;
                    text-align: center;

                    border: dotted 1px rgba(#333, 0);

                    .icon
                    {
                        display: inline-block;
                        width: 80px;
                        height: 80px;
                        background-image: url('../../assets/img/icon-bank-logo.png');
                        @include bg-offset-center();

                        border: solid 1px #d9d9d9;
                    }

                    cite
                    {
                        display: block;

                        @include ellipsis();
                    }

                    .btn-add,
                    .btn-delete
                    {
                        display: none;
                        position: absolute;
                        top: -10px;
                        right: 15px;
                        width: 50px;
                        height: 50px;

                        &:before
                        {
                            content: '';
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            width: 40px;
                            height: 40px;
                            background-image: url('../../assets/img/shortcut-editor/icon-add.png');

                            @include transform(translate(-50%, -50%));

                            @include bg-offset-center();
                        }
                    }
                    .btn-delete
                    {
                        &:before
                        {
                            width: 36px;
                            height: 36px;
                            background-image: url('../../assets/img/shortcut-editor/icon-delete.png');
                        }
                    }
                }
            }
        }

        .list.dragging
        {
            .item
            {
                .fun
                {
                    border: dotted 1px rgba(#333, 0.4);
                }
            }
            .item[draggable="true"]
            {
                .fun
                {
                    border: dotted 1px rgba(#f00, 0.6);
                }
            }
            .item.sortable-drag[draggable="true"]
            {
                .fun
                {
                    border: dotted 1px rgba(#333, 0.4);
                }
            }
        }

        .wran
        {
            padding: 5px 0px;
            font-size: 10px;
            text-align: center;
            color: #999;
            line-height: 1.4em;
        }
    }

    .fun-block + .fun-block
    {
        margin-top: 16px;
    }

    .shortcut-editor.show-editor
    {
        .fun-block
        {
            .list
            {
                .item
                {
                    .btn-add,
                    .btn-delete
                    {
                        display: block;
                    }
                }
            }
        }
    }

    .draggable-move 
    {
        transition: transform 0.3s;
    }
</style>





