<template>
    <div class="selector-bank-branch">
        <div class="fixed-content">
            <div class="search-bar">
                <div class="input-border">
                    <input type="search" placeholder="搜索..." v-model="search">
                </div>
            </div>
        </div>

        <div class="bank-branch">
            <better-scroll ref="bankBranchList" :data="bankBranchList" :probeType=" probeType " :listenScroll=" listenScroll "  @scroll="bankBranchListScroll" > 
                <div class="list">
                    <li class="item" v-for="( item, index ) in bankBranchList" >
                        <a href="javascript:;" @click="bankClick( item )">
                            <cite>{{ item.name }}</cite>
                            <span>{{ item.code }}</span>
                        </a>
                    </li>
                </div>
            </better-scroll>
        </div>
    </div>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

import * as serviceBankCtiy from '@/services/bank-branch';

export default {
    data() {
        return {
            routerFrom: '',
            probeType: 3,
            listenScroll: true,
            bankBranchList: [],
            bankBranchListSection: [],
            bswHeight: 0,
            bscHeight: 0,
            search: '',
            divisionHeight: 48,
            itemHeight: 80,
            hotItemHeight: 75,
            anchorHeight: 32
        };
    },
    created(){
        serviceBankCtiy.getBankBranchList({ id: 1, showLoading: '请等待...' }).then((res) => {
            let _temp = res.data.list;
            _temp.forEach( (item) => {
                item.name += '支行';
            });
             this.bankBranchList = _temp;
        }, (err) => {
            console.log( err );
        });
    },
    mounted (){
    },
    computed: {
    },
    components: {
    },
    methods: {
        _anchorHeight () {
            let flexible = window['lib'].flexible;
            return ( flexible.rem / flexible.standard ) * 32;
        },
        _anchorNext ( key ) {
            return this.anchorList[ this.anchorList.indexOf( key ) + 1 ];
        },
        _currentAnchor ( y ) {
            for (var i in this.anchorMap) {
                let _anchorNext = this._anchorNext( i ) || -1;
                if( y < this.anchorMap[i] && ( y > this.anchorMap[ _anchorNext ] || _anchorNext === -1 ) ) {
                    this.anchorActive = i;
                }
            }
        },
        _scrollTo ( anchor ) {
            // this.search = anchor;
            let _maxY = this.bswHeight - this.bscHeight;
		    let _y = Math.min(0, Math.max(_maxY, this.anchorMap[ anchor ]));
            this.$refs.bankBranchList.scrollTo(0, _y );
            this.anchorActive = anchor;
	    },
        bankClick ( data ) {
            this.$store.dispatch('updateBankBranch', data);
            this.$router.replace({ path: this.routerFrom.path });
        },
        bankBranchListScroll (data) {
            this._currentAnchor( data.y );
        }
    },
    beforeRouteEnter (to, from, next) {
        next((vm)=>{
            vm.routerFrom = from;
        });
    }
};
</script>



<style lang="scss" scoped>
    
    .selector-bank-branch
    {
        position: relative;
        padding-top: 80px;
        height: 100%;
    }
    .fixed-content
    {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 330px;
    }
    .search-bar
    {
        position: relative;
        padding: 13px 20px;
        height: 80px;
        background-color: #fff;

        @include mini-border(bottom);

        .input-border
        {
            position: relative;
            padding: 2px 5px;
            height: 54px;
            @include mini-border(all, #d9d9d9, before, 5px);
            
            input
            {
                position: relative;
                z-index: 9;
                display: block;
                padding: 10px;
                width: 100%;
                height: 50px;
                line-height: normal;
                border: none;
            }
        }
    }
    .bank-branch
    {
        position: relative;
        padding-bottom: 10px;
        height: 100%;
        background-color: #fff;

        .better-scroll-wrapper
        {
            position: relative;
            height: 100%;
            overflow: hidden;
        }

        .list
        {
            .item
            {
                position: relative;
                @include mini-border(top);
                a
                {
                    position: relative;
                    display: block;
                    padding: 10px 20px;
                    height: 100px;
                    overflow: hidden;

                    cite
                    {
                        display: block;
                        font-size: 13px;
                        line-height: 1.7em;
                    }
                    span
                    {
                        display: block;
                        color: #888;
                    }
                }
            }

            .item:first-child
            {
                &:before
                {
                    content: none;
                }
            }

        }
    }
    
</style>



