const state = {
    search: {
        accountType: '',
		action: '',
		accountNo: '',
        type: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateAccountOpeningSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearAccountOpeningSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            accountType: '',
            action: '',
            accountNo: '',
            type: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



