// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './app';
import router from './router';

// vuex
import Vuex from 'vuex';
Vue.use(Vuex);

// 基于 vuex 实现的 store
import store from './store';

// 第三方 filter
import vueFilter from 'vue-filter';
Vue.use(vueFilter);

// 自定义 filter
import filtes from './filters';

// 自定义 directive
import directives from './directives';

// 自定义的常用 验证规则
import validate from './assets/js/vue-validate';
Vue.use(validate);

// 自定义 jsbridge
import hybrid from './assets/js/vue-hybrid';
Vue.use(hybrid);

// 自定义 localStorage 封装
import ls from './assets/js/vue-ls';
Vue.use(ls);

// 自定义 flexible
import flexible from './assets/js/vue-flexible';
Vue.use(flexible);

// 挂载全局组件
import components from './components';
Vue.use(components);

// 引入 mint ui
import Mint from 'mint-ui-rem-dpr';
import 'mint-ui-rem-dpr/lib/style.css';
Vue.use(Mint);

import MyPlugin from '@/assets/js/public';
Vue.use(MyPlugin);

// 全局 css
import './assets/css/global.scss';

// FastClick
import FastClick from 'fastclick';
if ('addEventListener' in document) {
    document.addEventListener('DOMContentLoaded', function () {
        FastClick.attach(document.body);
    }, false);
}

// 模拟数据
import mock from './services/mock';

// vue touch
import touch from 'vue-touch';
Vue.use(touch, { name: 'v-touch' });

Vue.config.devtools = true;
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: { App }
});



