import * as types from '../mutation-types';

const state = {
    bankBranch: ''
};

// getters
const getters = {
};

// actions
const actions = {
    updateBankBranch: ({ commit }, data ) => {
        commit('UPDATE_BANK_BRANCH', data);
    },
    clearBankBranch: ({ commit }, data ) => {
        commit('CLEAR_BANK_BRANCH', data);
    }
};

// mutations
const mutations = {
    UPDATE_BANK_BRANCH (state, data) {
        state.bankBranch = data.name;
    },
    CLEAR_BANK_BRANCH (state, data) {
        state.bankBranch = '';
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



