const state = {
    search: {
        account: '',
        data: {}
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateDeviceRevocationSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearDeviceRevocationSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            account: '',
            data: []
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



