<template>
    <div class="bank-card">
        <ul class="card-list">
            <li class="item" v-for="( item, index ) in bankCardList">
                <a href="javascript:;" @click="bankCardClick( item )">
                    <i class="logo"></i>
                    <span v-if=" item.isDefault " class="is-default">主账户</span>
                    <div class="attr">
                        <cite class="account">{{ item.account }}</cite>
                        <span class="alias">{{ item.alias }}</span>
                        <span class="bank">{{ item.bankName }}</span>
                        <span class="type">{{ item.type }}</span>
                    </div>
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        bankCardList: {
            type: Array,
            default: []
        }
    },
    data() {
        return {};
    },
    created() {
    },
    methods: {
        bankCardClick ( item ) {
            this.$emit('bank-card-click', item);
        }
    }
};

</script>

<style lang="scss" scoped>

    .bank-card
    {
        .card-list
        {
            .item
            {
            
                a
                {
                    position: relative;
                    display: block;
                    padding: 10px 15px;
                    padding-left: 120px;
                    min-height: 140px;
                    background-color: #e7e7e7;
                    font-size: 12px;

                    @include border-radius(5px);
                }


                .logo
                {
                    position: absolute;
                    top: 5px;
                    left: 15px;
                    width: 80px;
                    height: 80px;

                    background-image: url('../../assets/img/icon-bank-logo.png');

                    @include bg-offset-center();
                }

                .is-default
                {
                    position: absolute;
                    bottom: 15px;
                    left: 15px;
                    padding: 3px 5px;
                    line-height: 1em;
                    border: solid 1px #333;
                    font-size: 11px;

                    @include border-radius(4px);
                }

                .attr
                {
                    cite,span
                    {
                        display: block;
                        padding: 2px 0px 4px 0px;
                    }

                    .account
                    {
                        font-size: 13px;
                    }

                    .type
                    {
                        position: absolute;
                        top: 20px;
                        right: 20px;
                    }
                }
            }

            .item + .item
            {
                margin-top: 20px;
            }
        }
    }
   
</style>


