const state = {
    search: {
        account: '',
		name: '',
		idCardType: '',
		idCard: ''
    }
};

// getters
const getters = {
};

// actions
const actions = {
    updateFinanceProductsRiskSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearFinanceProductsRiskSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
	        account: '',
			name: '',
			idCardType: '',
			idCard: ''
	    };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



