<template>
    <div class=" selector-transfer-aging" :class="{ show : currentShow }"   >
        <div class="mask" @click=" close()  "></div>
        <div class="content">
            <div class="hd">
                <a href="javascript:;" class="btn-cancel" @click=" close() ">取消</a>
                <h2>{{ title }} </h2>
            </div>
            <div class="bd">
                <ul class="list">
                    <li class="item" :class="{ checked: item.code == checkedCode  }" v-if=" item.active "  v-for="( item, index ) in usable " :key=" item.id ">
                        <a href="javascript:;" @click="change( item, index )" >
                            <cite :class="{ 'no-description' : !item.description } ">{{ item.text }}</cite>
                            <span v-if=" item.description " v-html=" item.description "></span>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>


export default {
    props: [
        'options'
    ],
    data() {
        return {
            currentShow: false,
            resolve: '',
            reject: '',
            promise: '',
            usable: [
                {
                    active: true,
                    key: 'instant',
                    code: '001',
                    text: '实时到账',
                    description: ''
                },
                {
                    active: true,
                    key: 'general',
                    code: '002',
                    text: '普通到账',
                    description: '5万以内实时受理，预计1-2小时到账<br>超出5万，仅工作日8:30 - 17:00 受理，到账以收款行为准'
                },
                {
                    active: true,
                    key: 'morrow',
                    code: '003',
                    text: '次日到账',
                    description: '将于次日（0点只后）汇出款项'
                }
            ]
        };
    },
    created(){
        this.usable.forEach((item, idnex) => {
            if( !this[ item.key ] ){
                item.active = false;
            }
        });
    },
    computed: {
        title () {
            return this.options.title || '选择转账方式';
        },
        checkedCode () {
           return this.options.checked ? this.options.checked : '';
        },
        instant () {
            return this.options.instant !== false;
        },
        general () {
            return this.options.general !== false;
        },
        morrow (){
            return this.options.instmorrowant !== false;
        }
    },
    components: {
    },
    methods: {
        change (item, index) {
            this.currentShow = false;
            this.resolve({ item, index });
        },
        close () {
            this.currentShow = false;
            this.reject();
        },
        show () {
            this.currentShow = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise;
        }
    }
};
</script>



<style lang="scss" scoped>

    $item-height: 80px;

    .selector-transfer-aging
    {
        position: fixed;
        top: 100%;
        left: 0px;
        right: 0px;
        bottom: 0px;
        z-index: 999;
        opacity: 0;
        

        @include transition(opacity 0.3s);

        &.show
        {
            top: 0px;
            opacity: 1;

            .content
            {
                top: auto;
                bottom: 0px;
            }
        }

        .mask
        {
            position: absolute;
            top: 0px;
            left: 0px;
            right: 0px;
            bottom: 0px;
            z-index: 1;
            background-color: $mask-rgba;
        }
    }

    .content
    {
        position: absolute;
        left: 0px;
        right: 0px;
        top: 100%;
        z-index: 9;
        background-color: #fff;

        .hd
        {
            position: relative;
            height: $item-height;
            line-height: $item-height;

            @include mini-border(bottom);
            h2
            {
                margin: 0px;
                font-size: 14px;
                font-weight: normal;
                text-align: center;
            }

            .btn-cancel
            {
                position: absolute;
                top: 0px;
                left: 0px;
                padding: 0px 20px;
                height: 100%;
                color: #4893E3;
            }
        }

        .bd
        {
        }
    }

    .list
    {
        padding-bottom: 30px;
        .item
        {
            a
            {
                display: block;
                position: relative;
                padding: 8px 20px 8px 20px;
                min-height: $item-height;

                @include mini-border(bottom);

                cite
                {
                    display: block;
                    color: #333;
                }
                cite.no-description
                {
                    line-height: $item-height;
                }
                span
                {
                    color: #888;
                    font-size: 10px;
                }
                
            }
        }
        .item.checked
        {
            a
            {
                &:after
                {
                    content: '';
                    position: absolute;
                    top: 50%;
                    right: 20px;

                    width: 40px;
                    height: 40px;
                    
                    @include bg-offset-center();
                    @include transform(translateY(-50%));

                    background-image: url('../../assets/img/selector/icon-item-checked.png');
                    
                }
            }
        }
    }
    
</style>



