import { Toast } from 'mint-ui-rem-dpr';
export default {
    install: function (Vue, options) {
        // 弹框消息
        Vue.prototype.$msg = function (msg, cl, duration) {
            Toast({
                message: msg,
                iconClass: cl || 'error',
                duration: duration || '1000'
            });
        };
    }
};


