import Vue from 'vue';

Vue.directive('number', {
    bind: (el, binding) => {
        let len = binding.expression || 100;
        el.addEventListener('keydown', function(e) {
            if( e.keyCode == 8 || e.keyCode == 13 ) {
                e.returnValue = true;
                return true;
            }
            if( this.value.length >= len ) {
                e.returnValue = false;
                return false;
            }
            if( !( e.keyCode >= 48 && e.keyCode <= 57 ) || ( e.keyCode >= 96 && e.keyCode <= 105 ) ) {
                e.returnValue = false;
                return false;
            }
        }, false);
    }
});


