import * as types from '../mutation-types';

const state = {
    bankName: ''
};

// getters
const getters = {
};

// actions
const actions = {
    updateBank: ({ commit }, data ) => {
        commit('UPDATE_BANK', data);
    },
    clearBank: ({ commit }, data ) => {
        commit('CLEAR_BANK', data);
    }
};

// mutations
const mutations = {
    UPDATE_BANK (state, data) {
        state.bankName = data.name;
    },
    CLEAR_BANK (state, data) {
        state.bankName = '';
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



