import * as types from '../mutation-types';

const state = {
    life: {
        Power: false,
        Water: false,
        Gas: false,
        Heat: false,
        Television: false
    },
    power: {
        item: '电费',
        area: '',
        company: '',
        companyName: '',
        no: '',
        name: '',
        money: ''
    },
    historyList: [
        {
            id: '01',
            area: "03",
            companyName: '蚌埠电力公司3',
            company: "033",
            href: "LifePower",
            item: "电费",
            name: "王小锤",
            mark: '我家的电费',
            no: "5654654"
        }, {
            id: '02',
            area: "03",
            company: "032",
            companyName: '蚌埠电力公司2',
            href: "LifeHeat",
            item: "热力费",
            name: "王小锤",
            mark: '妈妈家的电费',
            no: "3456754"
        }] // historyList为测试数据，生产不需要，只是模拟后台数据
};

// getters
const getters = {
    history: state => state.life,   // 判断是否显示该功能模块入口
    historyList: state => state.historyList
};

// actions
const actions = {
    showHistory: ({ commit }, data) => {
        commit('HISTORY', data);
    },
    setHistory: ({ commit }, data) => {
        commit('SET_HISTORY', data);
    },
    //  保存电费数据
    keepPowerInfo: ({ commit }, data) => {
        commit('KEEP_POWER_INFO', data);
    },
    //  清楚电费数据
    clearPowerInfo: ({ commit }) => {
        commit('CLEAR_POWER_INFO');
    }
};

// mutations
const mutations = {
    HISTORY(state, data) {
        state.historyList.push(data);
    },
    SET_HISTORY(state, data) {
        state.life[data.item] = data.status;
    },
    KEEP_POWER_INFO(state, data) {
        state.power = Object.assign(state.power, data);
    },
    CLEAR_POWER_INFO(state) {
        state.power = {
            item: '电费',
            area: '',
            company: '',
            companyName: '',
            no: '',
            name: '',
            money: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



