<template>
	<mt-button @click.native="submit" :size="size" :type="submitStatus ? 'primary' : 'default'" :disabled="!submitStatus">{{value}}</mt-button>

</template>

<script>

export default{
	props: {
		size: {
			type: String,
			default: 'normal'
		},
		submitStatus: {
			type: Boolean,
			default: false
		},
		value: {
			type: String,
			default: '确定'
		}

	},
	methods: {
		submit(){
			this.$emit('submit');
		}
	}
};

</script>

<style lang="scss">
	.mint-button--default{
		background-color: #666;
		color: #fff;
	}
</style>


