const state = {
	detail: {
        account: '',
		code: '',
		name: '',
        day: '',
        keep: '',
        profit: '',
        status: ''
	}
};

// getters
const getters = {
};

// actions
const actions = {
    updateApplyOrder: ({ commit }, data) => {
        commit('UPDATE', data);
    }
};

// mutations
const mutations = {
    UPDATE (state, data) {
        state[data.key] = data.value;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



