import * as types from '../mutation-types';

const state = {
    testIndexStoreText: '我来自 store ...'
};

// getters
const getters = {
};

// actions
const actions = {
    updateTestIndexStoreText: ({ commit }, data ) => {
        commit(types.UPDATE_TEST_INDEX_STORE_TEXT, data);
    }
};

// mutations
const mutations = {
    [types.UPDATE_TEST_INDEX_STORE_TEXT] (state, data) {
        state.testIndexStoreText = data;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



