<template>
    <div class="cell">

        <router-link tag="a" class="cell-list" :to="href">
            <div class="cell-wrap">

                <div v-if="hdTitle" class="cell-hd">
                    <!-- 一行使用hdTitle，所以v-if="hdTitle" -->
                    <span v-if="hdTitle" class="hd-title" v-text="hdTitle"></span>
                    <span v-if="hdDesc" class="hd-desc" v-text="hdDesc"></span>
                </div>

                <div class="cell-main">

                    <slot name="icon" class="icon">
                    </slot>

                    <div class="cell-title">
                        <span class="main-title" v-text="title"></span>
                        <span v-if="label" class="main-desc" v-text="label"></span>
                    </div>
                </div>

                <div class="cell-right">
                    <div class="link">
                        <span v-if="value" class="value" v-text="value"></span>
                        <span v-if="desc" class="sub-value" v-text="desc"></span>
                    </div>
                    <slot name="btn">

                    </slot>
                    <!--<div class="cell-swiper" v-if="manager">
                        <span v-for="item in swiper" @click.stop.prevent="edit(item)" :style="item.style" v-text="item.content"></span>
                    </div>-->
                </div>

                <i v-if="isLink" class="mint-cell-allow-right" :class="{'select': select}"></i>

            </div>
        </router-link>

    </div>
</template>

<script>
    /**
 * @module 自定义cell样式
 * @param {string|Object} [to] - 跳转链接
 * @param {Array} [swiper] 左滑可以编辑操作，传递具体操作参数(已经取消该方法)
 * @example [swipe] [{content: '修改备注',style: {color: '#000'}, handler: (cell) => this.editInfo(cell)}]
 * @param {string} [icon] - 图标（使用slot="icon"插槽）
 * @param {string} [hdTitle] - 最左边的title
 * @param {string} [hdDesc] - 最左边title下面的备注信息
 * @param {string&required} [title] - 主标题
 * @param {string} [label] - 备注信息
 * @param {string} [value] - 右侧显示文字
 * @param {string} [desc] - 右侧value下面的文字
 * @param {boolean} [isLink=false] - 可点击的链接
 * @param {boolean} [select=false] - 链接箭头向下，和isLink混合使用，表示下拉框样式
 *
 * @swiper  滑动事件
 * @content 滑动显示内容
 * @style 自定义样式（默认{color: #26a2ff;}）
 * @handler 回掉函数
 *
 * @example
 * <cell title="我的电费" value="金额" label="王小锤" desc="220.00元" hdTitle="今天" hdDesc="6-24" isLink>
 *     <img src="../../../assets/img/life/power.png" slot="icon" width="24" height="24">
 * </cell>
 */
    export default {
        name: 'cell',
        props: {
            to: [String, Object],
            hdTitle: String,
            hdDesc: String,
            title: {
                type: String,
                required: true
            },
            label: String,
            value: String,
            desc: String,
            isLink: Boolean,
            select: Boolean
        },
        data() {
            return {
                manager: false,
                start: { x: 0, y: 0 },  // 起始坐标
                distance: 0,    // 移动坐标距离差
                absDistance: -80  // 移动距离设定值

            };
        },
        computed: {
            href() {
                if (this.to === undefined || this.to === null) {
                    return "";
                } else {
                    return this.to;
                }
            }
        }
    };

</script>

<style lang="scss" scoped>
    $margin-top: 12px;
    $color: #888;
    .cell {
        background-color: #fff;
    }

    .cell-list {
        position: relative;
        background-color: inherit;
        box-sizing: border-box;
        color: inherit;
        min-height: 96px;
        display: block;
        overflow: hidden;
        text-decoration: none;
        @include mini-border(bottom);

        &::first-child {
            @include mini-border(top);
        }

        .cell-wrap {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            box-sizing: border-box;
            padding: 16px 14px;
            padding-right: 50px;
            font-size: 14px;
            line-height: 1;
            min-height: inherit;
            overflow: hidden;
            width: 100%;
            white-space: nowrap;

            .cell-hd+.cell-main {
                margin-left: 15px;
            }

            .cell-hd {
                color: $color;
                text-align: center;
                margin-left: 10px;

                .hd-title {
                    vertical-align: middle;
                }

                .hd-desc {
                    display: block;
                    font-size: 12px;
                    margin-top: $margin-top;
                }
            }

            .cell-main {
                -webkit-box-flex: 1;
                -ms-flex: 1;
                flex: 1;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                box-sizing: border-box;

                img+.cell-title {
                    margin-left: 15px;
                }

                .cell-title {
                    -webkit-box-flex: 1;
                    -ms-flex: 1;
                    flex: 1;

                    .main-title {
                        vertical-align: middle;
                    }

                    .main-desc {
                        color: $color;
                        display: block;
                        font-size: 12px;
                        margin-top: $margin-top;
                    }
                }
            }

            .cell-right {
                text-align: right;
                -webkit-transition: all ease .6s;
                -moz-transition: all ease .6s;
                transition: all ease .6s;

                .sub-value {
                    color: $color;
                    display: block;
                    font-size: 12px;
                    margin-top: $margin-top;
                }
            }

            .select {
                -webkit-transform: translateY(-50%) rotate(90deg);
                transform: translateY(-50%) rotate(90deg);
            }
        }
    }
</style>


