const state = {
    search: {
        account: '',
		beginDate: '',
		endDate: ''
    }
};

// getters
const getters = {
    transactionDetailSearch: state => state.search
};

// actions
const actions = {
    updateTransactionDetailSearch: ({ commit }, data ) => {
        commit('UPDATE_SEARCH', data);
    },
    clearTransactionDetailSearch: ({ commit }, data ) => {
        commit('CLEAR_SEARCH');
    }
};

// mutations
const mutations = {
    UPDATE_SEARCH (state, data) {
        state.search = data;
    },
    CLEAR_SEARCH (state, data) {
        state.search = {
            account: '',
		    beginDate: '',
		    endDate: ''
        };
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



